
import React, { Component } from 'react';

import MarkdownIt from 'markdown-it';

import Technology from './Technology'

const md = new MarkdownIt();

const defaultCarouselStyles =  {
    margin: '0 15px 0 15px',
    overflowX: 'auto',
    overflowY: 'hidden',
    whiteSpace: 'nowrap',
};

const expandedCarouselStyles =  { 
    ...defaultCarouselStyles, 
    ...{
        whiteSpace: 'normal',
    }
};

class Project extends Component {

    constructor (props) {
        super(props);

        this.state = {
            detailsHidden: true,
            showNotesMore: false,
            carouselStyles: defaultCarouselStyles
        }
    }

    renderCarousel (images) {

        if ( ! images || ! images.length ) {
            return <div>No images</div>;
        }

        const carousel_settings = {
            arrows: true,
            dots: false,
            infinite: false,
            speed: 500,
            slidesToShow: 1,
            slidesToScroll: 1
        };

        return images.map( (image_url, index) => <img key={`project_image_${index}`} src={image_url} style={{ width: '100%', height: 'auto', marginBottom: '14px'}}/> );

    }

    toggleNotesMore = () => {
        this.setState( (state) => ({ 
            showNotesMore: ! this.state.showNotesMore,
            carouselStyles: ! this.state.showNotesMore ? expandedCarouselStyles : defaultCarouselStyles
        }) )
    }

    render () {

        const {
            name,
            description,
            notes,
            technologies,
            status,
            project_url,
            repo_url
        } = this.props.data;

        const project_link = project_url ? <a href={project_url || '#'} target="_blank" rel="noopener noreferrer"><i className="fa fa-external-link" aria-hidden="true"></i>&nbsp;</a> : '';
        const repo_link = repo_url ? <a href={repo_url || '#'} target="_blank" rel="noopener noreferrer"><i className="fa fa-github" aria-hidden="true"></i>&nbsp;</a> : '';

        const images = this.props.data.images || [];

        const notes_parts = notes.split('####');
        const notes_essentials = notes_parts[0];
        const notes_more = notes_parts[1] || '';

        return (
            <div>
                <div className="title">
                    <div className="container">
                        <div className="row justify-content-between">
                            <div className="col-sm">
                                <h4>{name} <span style={{ fontSize: '0.8em' }}>{project_link} {repo_link}</span> <span style={{ fontSize: '0.5em' }}className="badge badge-pill badge-info">{status}</span></h4>
                            </div>
                            <div className="col-sm">
                                <ul className="technologies__list">
                                    {(technologies || []).map( 
                                        (techno, index, array) => {
                                            const colorWeight = 1; //1.4 - (index / array.length);
                                            const style= { 
                                                backgroundColor: `rgba(200,200,200,${ colorWeight } )`,
                                                color: `rgba(255,255,255,${ colorWeight } )` 
                                            }
                                            return <li key={`techno_${index}`}><Technology name={techno} style={style} /></li>
                                        } 
                                    )}
                                </ul>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="project__content">
                    <div className="container">
                        <div className="row">
                            <div className="col-sm">
                                <h6>
                                    {description} 
                                </h6>
                                <div className="project__details">
                                    <p>
                                        <span dangerouslySetInnerHTML={ { __html: md.renderInline(notes_essentials) }}></span>
                                        { notes_more ? (  <button className="btn btn-sm btn-link" onClick={this.toggleNotesMore}>
                                                { this.state.showNotesMore ? <i className="fa fa-arrow-circle-up" aria-hidden="true"></i> : <i className="fa fa-arrow-circle-down" aria-hidden="true"></i> }
                                            </button> ) : '' }
                                    </p>
                                    <p>
                                        <span dangerouslySetInnerHTML={ { __html: md.render(notes_more) }} style={{ display: this.state.showNotesMore ? 'block' : 'none' }}></span>
                                    </p>
                                </div>
                            </div>
                            <div className="col-lg-6 project__carousel" style={ this.state.carouselStyles }>
                                { this.renderCarousel(images) }
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );

    }

}

export default Project;



